<template>
<div>
<div id="popupwindow">
    <!-- <el-button type="text"  id="alertwindow" @click="openalert">点击打开 alert弹窗</el-button>
    <el-button type="text" v-if="confirmshow" id="confirmwindow" @click="openconfirm">点击打开 confirm弹窗</el-button>
    <el-button type="text" v-if="promptshow" id="promptwindow" @click="openprompt">点击打开 prompt弹窗</el-button> -->
    <p v-show="!btn1show">隐藏按钮 {{btn1timeshow}}s后出现</p>
    <el-button v-show="btn1show" type="primary" id="btn1" @click="openalert">隐藏按钮 3s后出现</el-button>
    <br>
    <p v-show="!btn2show">未生成的按钮 {{btn2timeshow}}s后才生成</p>
  <el-button  v-if="btn2show" type="success" id="btn2" @click="openalert">未生成的按钮 6s后才生成</el-button>
  <br>
  <el-button  v-show="btn3show" type="success" id="btn3" @click="openalert">{{btn3timeshow}}s后消失按钮</el-button>

    <!-- <el-button :plain="true" @click="openmessage" id="btn4">打开特殊弹窗</el-button>
    <iframe id='testiframe' src="/#/iframetest">
        
    </iframe>
    <el-button type="text" @click="getdatatest">get_test</el-button> -->
</div>
<!-- <div id="log">
    <span style="color:white;font-size:10px" v-html="msg"></span>
</div> -->
<img id="cat" src="../assets/cat.gif" />
</div>
</template>
<script>
// import { eventBus } from '../main'

export default {
    name: 'PopupWindow',
    data(){
        return{
            msg: '',
            count :0,
            promptshow: false,
            confirmshow: false,
            word_count :0,
            words: 'abcdefghijklmnopk',
            Timer: null,
            btn1show:false,
            btn2show:false,
            btn3show:true,
            btn1timeshow:3,
            btn2timeshow:6,
            btn3timeshow:9

        }
    },
    created(){
        this.delayshow()
        // this.showtext()
        // alert("update");
        // eventBus.$on('brotherSaid',(message) =>{
        //     this.count += 1;
        //     alert(message)
        // })
        


    },
    mounted(){
        this.Timer = setInterval(this.showtext,1000)
    },
    methods:{
        getdatatest(){
            var _this = this
            this.$axios.get("http://127.0.0.1:8082/hello",
                {}
            ).then(function(res){
                alert(res.data)
                console.log(res)
                _this.msg += res.data
                
            }).catch((err)=>{
                console.log(err)
            })
        },
        delayshow(){
            setTimeout(()=>{this.btn1show=true},3000)
            setTimeout(()=>{this.btn2show=true},6000)
            setTimeout(()=>{this.btn3show=false},9000)

            // setInterval(this.showtext)


        },
        showtext(){
            console.log("showtext")
            // setTimeout(()=>{this.msg += this.words[this.word_count]+'<br>'},100)
            setTimeout(()=>{
                    if(this.btn1timeshow==0){
                        return
                    }
                    this.btn1timeshow-=1

                    },10)
            setTimeout(()=>{
                    if(this.btn2timeshow==0){
                        return
                    }
                    this.btn2timeshow-=1

                    },10)
            setTimeout(()=>{
                    if(this.btn3timeshow==0){
                        return
                    }
                    this.btn3timeshow-=1

                    },10)

        },
        openalert(){
            
            alert("alert测试弹窗")
            this.count += 1
            this.msg += '<p>'+this.count+'.已确定alert弹窗</p>'
        },
        openconfirm(){
            var x = confirm("确定提交")
            if(x==true){
                this.count += 1
                this.msg += '<p>'+this.count+'.confirm弹窗已确定提交</p>'
            }
            else{
                this.count += 1
                this.msg += '<p>'+this.count+'.confirm弹窗已取消提交</p>'
            }
        },
        openprompt(){
            var a =prompt("请输入内容"); 
            if(a){
                this.count += 1
                this.msg += '<p>'+this.count+'.promt弹窗输入的内容是：'+a+'</p>'
            }
            else{
                if(a===""){
                    this.count += 1
                    this.msg += '<p>'+this.count+'.prompt弹窗已输入为空</p>'
                }
                else{
                    this.count += 1
                    this.msg += '<p>'+this.count+'.prompt弹窗已取消输入</p>'
                }
                
            }
            
        },
        openmessage(){
            this.$alert('这是一个特殊的弹窗', '标题名称', {
            confirmButtonText: '确定',
            callback: action => {
                this.$message({
                type: 'info',
                message: `action: ${ action }`
                });
                if(action=='confirm'){
                    this.count += 1
                    this.msg += '<p>'+this.count+'.特殊弹窗已确认</p>'
                }
                else{
                    this.count += 1
                    this.msg += '<p>'+this.count+'.特殊弹窗已取消</p>'
                }
                
            }
            });
        }
    }
}
</script>
<style scoped>
iframe{
    width:100%;
    border-width: 0px;
}

#popupwindow{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    border-width: 1px;
    border-style:dashed;
    border-color:aqua;
    position:relative;
    top:0px;

}
#btn4{
    width:150px;
    /* margin-left:30px; */
}
#log{
    text-align: left;
    border-width: 1px;
    border-style:solid;
    border-color:black;
    background-color:black;
    position: relative;
    min-height: 100px;
    max-height: 200px;
    width: 50%;
    top:10px;
    /* overflow:scroll; */

    
}
#cat{
    position: fixed;
    bottom:10px;
    right:10px;
}

</style>
